import { Component, OnInit } from '@angular/core';
import { CommonService } from 'src/app/service/common.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-inbound-new',
  templateUrl: './inbound-new.component.html',
  styleUrls: ['./inbound-new.component.scss']
})
export class InboundNewComponent implements OnInit {

  constructor(private service:CommonService, private route:ActivatedRoute) { }
  
  products = [];

  ngOnInit() {

    this.route.queryParams.subscribe(data=>{
      if(data.product != null)
      {
        this.products.push(this.service.getProduct(data.product));
      }
    })
    
  }

}
